import { Fragment, FunctionComponent, MouseEvent, useCallback, useContext, useMemo } from 'react';

import MetaList from '../MetaList';
import SummaryItem from '../SummaryItem';
import { ForwardProps } from '../../types';
import Flex from '../Flex';
import Icon, { registerIcon } from '../Icon';
import * as caretLeftIcon from '../Icon/icons/caret-left.icon';
import * as caretRightIcon from '../Icon/icons/caret-right.icon';
import * as checkIcon from '../Icon/icons/check.icon';
import Text from '../Text';
import { Count } from '../Badges';
import BareButton from '../Button/BareButton';
import { replaceMatchWithElement, withTestIds } from '../../utils';
import { useElement, useI18n, useDirection, useLiveLog, useTestIds } from '../../hooks';
import Tooltip from '../Tooltip';
import Mark from '../Mark';
import Button from '../Button';

import menuHelpers from './helpers';
import { AcceptedMouseEventElement, MenuItemProps } from './Menu.types';
import MenuContext from './Menu.context';
import { StyledAncestors, StyledCheckMarkIcon, StyledMenuItem } from './Menu.styles';
import { getMenuItemTestIds } from './Menu.test-ids';

registerIcon(caretLeftIcon, caretRightIcon, checkIcon);

const DrillDown = ({
  expandHandler
}: {
  expandHandler?: (e: MouseEvent<AcceptedMouseEventElement>) => void;
}) => {
  const { end } = useDirection();

  const icon = <Icon name={`caret-${end}`} />;

  return expandHandler ? (
    <Button as='span' icon variant='simple' aria-hidden onClick={expandHandler}>
      {icon}
    </Button>
  ) : (
    icon
  );
};

const AncestorPath = ({ ancestors = [] }: Pick<MenuItemProps, 'ancestors'>) => {
  const truncatedPath = ancestors.length > 2;
  const pathParts = truncatedPath ? [ancestors[0], ancestors[ancestors.length - 1]] : ancestors;

  const { end } = useDirection();

  return (
    <Flex
      container={{ gap: 0.5, alignItems: 'center' }}
      as={StyledAncestors}
      title={ancestors.map(a => (menuHelpers.isItem(a) ? a.primary : a.label)).join(' > ')}
    >
      {pathParts.map((ancestor, i, arr) => {
        const label = menuHelpers.isItem(ancestor) ? ancestor.primary : ancestor.label;
        return (
          <Fragment key={label}>
            <Text variant='secondary'>{label}</Text>
            {i < arr.length - 1 && (
              <>
                <Text variant='secondary'>
                  <Icon name={`caret-${end}`} />
                </Text>
                {truncatedPath && (
                  <>
                    <Text variant='secondary'>â€¦</Text>
                    <Text variant='secondary'>
                      <Icon name={`caret-${end}`} />
                    </Text>
                  </>
                )}
              </>
            )}
          </Fragment>
        );
      })}
    </Flex>
  );
};

const MenuItem: FunctionComponent<MenuItemProps & ForwardProps> = ({
  testId,
  id,
  primary,
  secondary,
  ancestors,
  visual,
  count,
  items,
  selected,
  partial,
  href,
  tooltip,
  onClick,
  onExpand,
  disabled,
  role = 'menuitem',
  ...restProps
}: MenuItemProps & ForwardProps) => {
  const t = useI18n();
  const {
    mode,
    onItemClick,
    accent,
    variant: menuVariant,
    setFocusDescendant,
    getScopedItemId,
    arrowNavigationUnsupported
  } = useContext(MenuContext);

  const testIds = useTestIds(testId, getMenuItemTestIds);

  const { announcePolite } = useLiveLog();

  const selectionMode = mode === 'single-select' || mode === 'multi-select';
  const selectableParent = items && typeof selected === 'boolean';
  const itemId = useMemo(() => getScopedItemId(id), [id, getScopedItemId]);

  const itemCountId = `${id}-count`;
  const secondaryId = `${id}-secondary`;

  const clickHandler = useCallback(
    (e: MouseEvent<AcceptedMouseEventElement>) => {
      const mouseClick = e.detail !== 0;

      if (mouseClick) {
        const target = e.target as HTMLElement;
        const focusAtEl =
          target.getAttribute('role') === role ? target : target.closest(`li[role="${role}"]`);

        if (focusAtEl) setFocusDescendant(focusAtEl as HTMLElement);
      }
      onClick?.(id, e);
      onItemClick?.(id, e);
    },
    [onClick, onItemClick, id]
  );

  const expandHandler = useCallback(
    (e: MouseEvent<AcceptedMouseEventElement>) => {
      onExpand?.(id, e);
      e.stopPropagation();
    },
    [onExpand, id]
  );

  let accentedPrimary;
  if (accent && !items) {
    const accentRegex = typeof accent === 'function' ? accent(primary) : accent;
    accentedPrimary = replaceMatchWithElement(primary, accentRegex, str => {
      return <Mark>{str}</Mark>;
    });
  }

  const secondaryContent = ancestors ? (
    <AncestorPath ancestors={ancestors} />
  ) : (
    secondary && <MetaList items={secondary} id={secondaryId} />
  );

  const [summaryItemRef, setSummaryItemRef] = useElement();

  const summaryItem = (
    <>
      <SummaryItem
        ref={setSummaryItemRef}
        primary={accentedPrimary || (!items ? <Text>{primary}</Text> : primary)}
        secondary={secondaryContent}
        visual={visual}
        actions={
          items || count ? (
            <Flex container={{ gap: 1, alignItems: 'center' }}>
              {count !== undefined && (
                <Count id={itemCountId} aria-label={t('menu_item_count', [count])}>
                  {count}
                </Count>
              )}
              {items && <DrillDown expandHandler={selectableParent ? expandHandler : undefined} />}
            </Flex>
          ) : undefined
        }
        container={{
          colGap: 1
        }}
      />
      {tooltip && (
        <Tooltip target={summaryItemRef} hideDelay='none' showDelay='none' portal>
          {tooltip}
        </Tooltip>
      )}
    </>
  );

  const itemLabel = useMemo(() => {
    let label = '';
    let collapsedLabel = '';
    if (selectableParent && selectionMode) {
      const selectedParentItemLabel = selected
        ? t('selected_noun', [primary])
        : t('unselected_noun', [primary]);
      collapsedLabel = t('menu_collapsed', [selectedParentItemLabel]);
    } else {
      collapsedLabel = t('menu_collapsed', [primary]);
    }
    if (selectionMode && !items) {
      label = selected ? t('selected_noun', [primary]) : t('unselected_noun', [primary]);
    } else {
      label = primary;
    }
    if (items) {
      return arrowNavigationUnsupported
        ? t('menu_item_shift_space_expand_collapse', [collapsedLabel])
        : t('menu_item_expand_arrow', [collapsedLabel]);
    }
    return label;
  }, [selected, primary, items]);

  const ariaDescribedBy = useMemo(() => {
    const hasSecondary = !!secondary;
    let idString: string;

    if (hasSecondary) {
      idString = secondaryId;
      if (count !== undefined && count >= 0) {
        idString = `${idString} ${itemCountId}`;
      }
    } else if (count !== undefined && count >= 0) {
      idString = itemCountId;
    } else idString = '';

    return idString;
  }, [items, secondary, itemCountId, secondaryId]);

  return (
    <Flex
      data-testid={testIds.root}
      {...restProps}
      container={{ alignItems: 'center', justify: 'between', gap: 1 }}
      id={itemId}
      as={StyledMenuItem}
      aria-label={itemLabel}
      aria-describedby={ariaDescribedBy}
      role={role}
      aria-disabled={disabled}
      data-expand={!!items}
      tabIndex={-1}
      disabled={disabled}
      onMouseDown={(e: MouseEvent<HTMLButtonElement | HTMLAnchorElement>) => {
        e.preventDefault();
      }}
      onClick={(e: MouseEvent<AcceptedMouseEventElement>) => {
        if (disabled) {
          return;
        }
        /** Announce only on keyboard selection since mouse selection will automatically detect and act on the DOM aria attribute changes */
        if (e.detail === 0 && selected !== undefined) {
          announcePolite({
            /** selected is still not updated so putting the reverse logic. */
            message: !selected ? t('selected_noun', [primary]) : t('unselected_noun', [primary]),
            type: 'acknowledgement'
          });
        }
        return items && typeof selected !== 'boolean' ? expandHandler(e) : clickHandler(e);
      }}
      onMouseEnter={menuVariant === 'flyout' ? expandHandler : undefined}
      href={href}
      mode={mode}
      isParentItem={!!items}
      aria-selected={!!selected}
      selectableParent={selectableParent}
    >
      {selectionMode && (!items || selectableParent) && (
        <Icon as={StyledCheckMarkIcon} name='check' />
      )}

      {href && !selectionMode ? (
        <BareButton href={href} tabIndex='-1'>
          {summaryItem}
        </BareButton>
      ) : (
        summaryItem
      )}
    </Flex>
  );
};

export default withTestIds(MenuItem, getMenuItemTestIds);
